import { motion } from 'framer-motion';
import { useTypewriter } from 'react-simple-typewriter';
import CyberGame from '@components/CyberGame';
import { useState } from 'react';

const Home = () => {
  const [showGame, setShowGame] = useState(false);
  const [text] = useTypewriter({
    words: ['Gameplay Programmer', 'Engine Tools Developer', 'Problem Solver'],
    loop: true,
    delaySpeed: 2000,
  });

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-center"
      >
        <h1 className="text-4xl md:text-6xl font-bold mb-4">
          <span className="text-cyber-orange">Cyber</span>punk Developer
        </h1>
        <p className="text-xl md:text-2xl mb-8">
          <span className="text-cyber-orange">{text}</span>
        </p>
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          className="bg-cyber-orange text-black px-8 py-3 rounded-lg font-bold"
          onClick={() => setShowGame(true)}
        >
          Play Game
        </motion.button>
      </motion.div>
      <CyberGame isVisible={showGame} onClose={() => setShowGame(false)} />
    </div>
  );
};

export default Home; 